import React from 'react';

const Contact = () => {
  return (
    <React.Fragment>
      {/* ======= Contact Section ======= */}
      <section id='contact' className='contact'>
        <div className='container'>
          <div className='section-title'>
            <h2>Contact</h2>
            <p>Connect with Me</p>
          </div>
          <div className='row mt-2'>
            <div className='col-md-6 mt-4 d-flex align-items-stretch'>
              <div className='info-box'>
                <i className='bx bx-envelope' />
                <h3>Email</h3>
                <p>riko.trihendrawan [at] gmail [dot] com</p>
              </div>
            </div>
            <div className='col-md-6 mt-4 d-flex align-items-stretch'>
              <div className='info-box'>
                <i className='bx bx-phone-call' />
                <h3>Phone</h3>
                <p>+62 853 1428 5015</p>
              </div>
            </div>

            <div className='col-md-6 mt-4 d-flex align-items-stretch'>
              <div className='info-box'>
                <i className='bx bx-home-alt' />
                <h3>Homepage</h3>
                <p>
                  <a
                    style={{ color: 'white' }}
                    href='https://rikotrihendrawan.github.io'
                  >
                    rikotrihendrawan.github.io
                  </a>
                </p>
              </div>
            </div>
            <div className='col-md-6 mt-4 d-flex align-items-stretch'>
              <div className='info-box'>
                <i className='bx bx-share-alt' />
                <h3>Social Profiles</h3>
                <div className='social-links'>
                  <a
                    href='https://twitter.com/rikotrihendrawan'
                    className='twitter'
                  >
                    <i className='bi bi-twitter' />
                  </a>
                  <a
                    href='https://instagram.com/rikotrihendrawan'
                    className='instagram'
                  >
                    <i className='bi bi-instagram' />
                  </a>
                  <a
                    href='https://linkedin.com/in/rikotrihendrawan'
                    className='linkedin'
                  >
                    <i className='bi bi-linkedin' />
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div class='row justify-content-between'>
            <div className='col-md-6 mt-4'>&nbsp;</div>
            <div className='col-md-6 mt-4'>
              <form className='php-email-form mt-4'>
                <div className='row'>
                  <div className='col-md-6 form-group'>
                    <input
                      type='text'
                      name='name'
                      className='form-control'
                      id='name'
                      placeholder='Your Name'
                      required
                    />
                  </div>
                  <div className='col-md-6 form-group mt-3 mt-md-0'>
                    <input
                      type='email'
                      className='form-control'
                      name='email'
                      id='email'
                      placeholder='Your Email'
                      required
                    />
                  </div>
                </div>
                <div className='form-group mt-3'>
                  <input
                    type='text'
                    className='form-control'
                    name='subject'
                    id='subject'
                    placeholder='Subject'
                    required
                  />
                </div>
                <div className='form-group mt-3'>
                  <textarea
                    className='form-control'
                    name='message'
                    rows={5}
                    placeholder='Message'
                    required
                    defaultValue={''}
                  />
                </div>
                <div className='my-3'>
                  <div className='loading'>Loading</div>
                  <div className='error-message' />
                  <div className='sent-message'>
                    Your message has been sent. Thank you!
                  </div>
                </div>
                <div className='text-end'>
                  <button type='submit'>Send Message</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
      {/* End Contact Section */}
    </React.Fragment>
  );
};

export default Contact;
